import React from "react";
import axios from "axios";
import swal from "sweetalert";
import Navbar1 from "../Component/backbutton";
import { useHistory, useParams } from "react-router";

export default function Update() {
  const [imagePreview, setImagePreview] = React.useState("");
  const [disableSubmit, setDisableSubmit] = React.useState(true);
  const [isLoading, setIsLoading] = React.useState(false);
  let { slug } = useParams();
  console.log(slug);
 
  const [values, setValues] = React.useState({
    judul_buku: "",
    cover: "",
    nama_pengarang: "",
    kode_penulis: 20002,
    nama_penerbit_buku: "",
    ketebalan_buku: "",
    tahun_terbit_buku: "",
    sinopsis: "",
  });

  const [error, setError] = React.useState({
    judul_buku: "",
    cover: "",
    nama_pengarang: "",
    kode_penulis: 20002,
    nama_penerbit_buku: "",
    ketebalan_buku: "",
    tahun_terbit_buku: "",
    sinopsis: "",
  });
  const getDetail = async () => {
    const url = `https://api-react-2.herokuapp.com/api/perpustakaan/${slug}?kode=20002`;
    try {
      let respond = await axios.get(url);
      console.log(respond)
      setValues((values) => ({
        ...values,
        judul_buku: respond.data.data?.judul_buku,
        cover: respond.data.data?.cover,
        nama_pengarang: respond.data.data?.nama_pengarang,
        nama_penerbit_buku: respond.data.data?.nama_penerbit_buku,
        ketebalan_buku: respond.data.data?.ketebalan_buku,
        tahun_terbit_buku: respond.data.data?.tahun_terbit_buku,
        slug: respond.data.data?.slug,
        kode_penulis: respond.data.data?.kode_penulis,
        sinopsis: respond.data.data?.sinopsis,
        id: respond.data.data?.id,
      }));
      setImagePreview(respond.data.data.cover);
    } catch {}
  };
  React.useEffect(() => {
    getDetail();
  }, []);
  let history = useHistory();

  const handleChange = (e) => {
    const key = e.target.id;
    const value = e.target.value;
    setValues((values) => ({
      ...values,
      [key]: value,
    }));
    if (key === "judul_buku") {
      if (value === "") {
        return (error.judul_buku = "judul_buku Tidak Boleh Kosong");
      } else {
        error.judul_buku = "";
      }
    }
    if (key === "nama_penerbit_buku") {
      if (value === "") {
        return (error.nama_penerbit_buku =
          "nama_penerbit_buku Tidak Boleh Kosong");
      } else {
        error.nama_penerbit_buku = "";
      }
    }
    if (key === "nama_pengarang") {
      if (value === "") {
        return (error.nama_pengarang = "nama pengarang Tidak Boleh Kosong");
      } else {
        error.nama_pengarang = "";
      }
    }
    if (key === "ketebalan_buku") {
      if (value === "") {
        return (error.ketebalan_buku = "ketebalan buku Tidak Boleh Kosong");
      } else {
        error.ketebalan_buku = "";
      }
    }
    if (key === "tahun_terbit_buku") {
      if (value === "") {
        return (error.tahun_terbit_buku =
          "tahun terbit Tidak Boleh Kosong");
      } else {
        error.tahun_terbit_buku = "";
      }
    }
    if (key === "sinopsis") {
      if (value === "") {
        return (error.sinopsis = "sinopsis Tidak Boleh Kosong");
      } else if (value.replace(/\s/g, "").length < 10) {
        error.sinopsis = "sinopsis wajib 10 huruf";
      } else {
        error.sinopsis = "";
      }
    }
    disableSubmitHandle();
  };

  const disableSubmitHandle = () => {
    if (
      error.judul_buku === "" &&
      error.cover === "" &&
      error.nama_penerbit_buku === "" &&
      error.ketebalan_buku === "" &&
      error.nama_pengarang === "" &&
      error.ketebalan_buku === "" &&
      error.tahun_terbit_buku === "" &&
      error.sinopsis === ""
    ) {
      return setDisableSubmit(false);
    } else {
      return setDisableSubmit(true);
    }
  };
  const handleUpload = (e) => {
    e.preventDefault();
    let reader = new FileReader();

    setValues((values) => ({
      ...values,
      cover: e.target.files[0],
    }));
    reader.onloadend = () => {
      if (reader.readyState === 2) {
        setImagePreview(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
    console.log(imagePreview);
  };
  console.log(values);
  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("ok");
    const url = `https://api-react-2.herokuapp.com/api/perpustakaan/${slug}?kode=8101`;

    const formData = new FormData();
    formData.append("judul_buku", values.judul_buku);
    formData.append("cover", values.cover);
    formData.append("kode_penulis", values.kode_penulis);
    formData.append("nama_pengarang", values.nama_pengarang);
    formData.append("nama_penerbit_buku", values.nama_penerbit_buku);
    formData.append("ketebalan_buku", values.ketebalan_buku);
    formData.append("tahun_terbit_buku", values.tahun_terbit_buku);
    formData.append("sinopsis", values.sinopsis);
    formData.append("user_id", values.user_id);
    formData.append("_method", "put");
    try {
      let result = await axios.post(url, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });
      setIsLoading(false);
      console.log(result);
      console.log("nama_pengarang");
      swal("Good job!", "You clicked the button!", "success");
      history.push("/home");
    } catch (err) {
      console.log(err);
      setIsLoading(false);
    }
  };
  console.log(values);

  return (
    <React.Fragment>
      <div className="h-screen absolute w-full flex justify-center bg-gray-50 ">
        <div className="w-full md:2/5 lg:w-2/5">
          <div className="h-20 w-full grid grid-cols-1 gap-2 bg-blue-600 text-white rounded-b-lg">
            <Navbar1></Navbar1>
          </div>
          <form onSubmit={handleSubmit} className="mt-5">
            <div className="grid gap-1 pb-5">
              <input
                placeholder="Judul Buku"
                className="border pt-3 pb-3 pl-2 bg-gray-300 rounded-md"
                onChange={handleChange}
                onBlur={handleChange}
                name="judul_buku"
                value={values.judul_buku}
                id="judul_buku"
                type="text"
              />
            </div>

            <div className="grid gap-1 gap-x-3">
              {" "}
              <input
                name="cover"
                onChange={handleUpload}
                className="border pt-3 pb-3 pl-2 bg-gray-300 rounded-md"
                type="file"
                htmlFor="cover"
              />
            </div>
            <div className="">
              <img
                className="h-20 w-25 sm:h-40 w-45 rounded-md"
                src={imagePreview}
                alt=""
              />
            </div>
            <div>
              <div className="grid gap-1 pb-5">
                <textarea
                  className="border pt-3 pb-3 pl-2 bg-gray-300 rounded-md"
                  name="sinopsis"
                  id="sinopsis"
                  cols="30"
                  rows="10"
                  value={values.sinopsis}
                  onChange={handleChange}
                  onBlur={handleChange}
                  placeholder="Sinopsis"
                ></textarea>
              </div>
              <div className="grid gap-1 pb-5">
                <input
                  className="border pt-3 pb-3 pl-2 bg-gray-300 rounded-md"
                  onChange={handleChange}
                  onBlur={handleChange}
                  name="judul_buku"
                  value={values.nama_pengarang}
                  id="nama_pengarang"
                  type="text"
                  placeholder="Nama Pengarang Buku"
                />
              </div>
              <div className="grid gap-1 pb-5">
                <input
                  className="border pt-3 pb-3 pl-2 bg-gray-300 rounded-md"
                  onChange={handleChange}
                  onBlur={handleChange}
                  name="judul_buku"
                  value={values.nama_penerbit_buku}
                  id="nama_penerbit_buku"
                  type="text"
                  placeholder="Nama Penerbit"
                />
              </div>
              <div className="grid gap-1 pb-5">
                <input
                  className="border pt-3 pb-3 pl-2 bg-gray-300 rounded-md"
                  onChange={handleChange}
                  onBlur={handleChange}
                  name="judul_buku"
                  value={values.tahun_terbit_buku}
                  id="tahun_terbit_buku"
                  type="text"
                  placeholder="Tahun Terbit"
                />
              </div>
              <div className="grid gap-1 pb-5">
                <input
                  className="border pt-3 pb-3 pl-2 bg-gray-300 rounded-md"
                  onChange={handleChange}
                  onBlur={handleChange}
                  name="judul_buku"
                  value={values.ketebalan_buku}
                  id="ketebalan_buku"
                  type="text"
                  placeholder="Ketebalan Buku"
                />
              </div>
              <button
                onClick={() => {
                  setIsLoading(true);
                }}
                disabled={disableSubmit}
                type="submit"
                className={`inline-flex p-3 mt-10 w-full ${
                  disableSubmit
                    ? "bg-red-600"
                    : "bg-blue-500 hover:bg-blue-600 focus:bg-blue-700"
                } items-center justify-center uppercase font-bold text-white`}
              >
                {isLoading ? "Menyimpan data....." : "Simpan"}
              </button>
            </div>
          </form>
        </div>
      </div>
    </React.Fragment>
  );
}
