import React from "react";
import { useHistory } from "react-router";
import axios from "axios";
import swal from "sweetalert";
export default function Isi(props) {
  let history = useHistory();
  const [isLoading, setIsLoading] = React.useState(false);
  const deleteHandle = async (id) => {
    const url = `https://api-react-2.herokuapp.com/api/perpustakaan/${id}?kode=20002`;
    try {
      await axios.delete(url);
      props.getArtikel();
      setIsLoading(false);
    } catch (err) {
      setIsLoading(false);
    }
  };
  return (
    <React.Fragment>
      <div className="flex h-full py-10 rounded-lg bg-gray-100 hover:bg-blue-600 hover: hover:text-white ">
        <div className="items-center w-2/6">
          <img
            src={props.cover}
            className="hover: rounded-lg motion-safe:hover:scale-110 hover:shadow-md items-center"
          />
        </div>
        <div className=" items-center justify-self-start">
          <div className="ml-5">
            <h1 className="font-bold md:font-medium sm:font-bold lg:text-6xl sm:text-lg md:text-lg pb-8 object-left-top">
              {props.title}
            </h1>
            <p className="capitalize text-gray-400">{props.author}</p>
            <p className="capitalize text-gray-400">{props.publisher}</p>
            <p className="capitalize text-gray-400">
              {props.realese}, {props.page} Halaman
            </p>
            <div className="flex gap-1">
              <div
                onClick={() => {
                  history.push(`/home/update/${props.id}`);
                }}
                className="flex w-full object-left-bottom pt-6 items-center justify-between  border bg-green-600 transform motion-safe:hover:scale-110"
              >
                <h3 className="font-bold text-center">Update</h3>
              </div>
              <div
                onClick={() => {
                  swal({
                    title: "Are you sure?",
                    text: "Once deleted, you will not be able to recover this imaginary file!",
                    icon: "warning",
                    buttons: true,
                    dangerMode: true,
                  }).then((willDelete) => {
                    if (willDelete) {
                      swal("Poof! Your imaginary file has been deleted!", {
                        icon: "success",
                      });
                      deleteHandle(props.id);
                      setIsLoading(true);
                    } else {
                      swal("Your imaginary file is safe!");
                    }
                  });
                }}
                className="flex w-full object-left-bottom pt-6 items-center justify-between border bg-red-600 transform motion-safe:hover:scale-110"
              >
                 <h3 className="font-bold justify-center">Delete</h3>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
