import React from "react";

export default function Searchbox(params) {
  return (
    <div className="w-full md:w-auto h-32 pt-7  flex justify-center pb-4 bg-red-600 rounded-b-lg">
      <section className="px-3 py-3 h-16 flex justify-between gap-10 w-auto md:w-2/5 lg:w-2/5 bg-white rounded-md">
        <div>
          <input
            type="text"
            className="pt-3 pb-2 pr-2 pl-2"
            placeholder="Search Book"
          />
        </div>
      </section>
    </div>
  );
}
