import React, { Component } from "react";
import { Link, useParams, useHistory } from "react-router-dom";
import axios from "axios";
import Searchbox from "../Component/searchbox";
import Isi from "../Component/isi";
import Loading from "../loading";

export default function Home(props) {
    let history = useHistory()
  const [books, setbooks] = React.useState([]);
  const [isLoading, setIsLoading] = React.useState(true);
  const getBooks = async () => {
    const url =
      "https://api-react-2.herokuapp.com/api/perpustakaan?keyword=&kode=20002";
    try {
      let respond = await axios.get(url);
      console.log(respond.data.data.data);
      setbooks(respond.data.data.data);
      setIsLoading(false);
    } catch (err) {
      console.log(err, "error");
      setIsLoading(false);
    }
  };
  React.useEffect(() => {
    getBooks();
  }, []);
  return (
    <React.Fragment>
      <div className=" h-auto w-full justify-center flex bg-gray-50">
        <div className="w-full">
          <div>
            <Searchbox />
          </div>
          <div className="ml-8 mt-3 mb-4">
            <h1 className="font-bold text-lg">List Buku</h1>
          </div>
          <div>
            <section className="static h-screen m-3 pt-1 pr-1 pb-11 box-border justify-center shadow-sm">
              <div className="grid grid-cols-1 w-full">
               {isLoading ? (
                 <React.Fragment>
                 <Loading></Loading>
               </React.Fragment>
               ) : (
                books.map((book, index) => (
                  <Isi
                    key={index}
                    id={book.id}
                    title={book.judul_buku}
                    author={book.nama_pengarang}
                    publisher={book.nama_penerbit_buku}
                    realese={book.tahun_terbit_buku}
                    page={book.ketebalan_buku}
                    sinopsis={book.sinopsis}
                    cover={book.cover}
                    id={book.id}
                  />
                ))
               )}
              </div>
            </section>
          </div>
          <div className="static">
            <button
            onClick={() => {
                history.push(`/home/tambah`)
            }}
              className="inline-flex p-3 mt-10 w-full bg-red-500 hover:bg-red-600 focus:bg-red-700 items-center justify-center uppercase font-bold text-white"
              type="submit"
            >
              Add Book
            </button>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
